<template>
	<div>
		<mt-header title="" class="header-black">
			<div slot="left" >
				<mt-button icon="back"></mt-button>
			</div>
			<mt-button slot="right" @click.native="changeHandle">{{handle}}</mt-button>
		</mt-header>
		
		<mt-navbar v-model="selected" class="header-black">
			<mt-tab-item id="1" class="com-nav" style="color: white;"></mt-tab-item>
			<mt-tab-item id="2" class="com-nav" style="color: white;"></mt-tab-item>
		</mt-navbar>
		<!-- tab-container -->
		<mt-tab-container v-model="selected">
			<mt-tab-container-item id="1">
				<!---->
				<cartshop :cart_handle="cart_handle" :changeIdx="changeIdx"></cartshop>
			</mt-tab-container-item>
			<mt-tab-container-item id="2">
				<!---->
				<cartcontent></cartcontent>
			</mt-tab-container-item>
		</mt-tab-container>
	</div>
</template>

<script>
	import { Header } from "mint-ui";
	import { Navbar, TabItem } from 'mint-ui';
	import cartcontent from "./cart/cartContent.vue";
	import cartshop from "./cart/cartShop.vue";
	export default {
		data() {
			return {
				selected:'1',
				handle:'',
				changeIdx:false,
				cart_handle:""
			}
		},
		components: {
			Header,
			Navbar,
			TabItem,
			cartshop,
			cartcontent
		},
		methods:{
			changeHandle(){
				this.handle=this.changeIdx?'':"";
				this.cart_handle=this.changeIdx?'':"";
				this.changeIdx=!this.changeIdx;
			}
		},
		created(){
			
		}
	}
</script>

<style>
	.header-black {
		background: #333333;
	}
	
	.com-nav {
		font-size: 16px !important;
	}
	
	.is-selected {
		color: orangered !important;
		border-color: orange !important;
	}
</style>